@tailwind base;
@tailwind components;
@tailwind utilities;

/* ProgressBar.css */
@keyframes loading {
    0% {
        transform: translateX(-100%);
    }
    100% {
        transform: translateX(100%);
    }
}

.loading-animation {
    animation: loading 1.5s linear infinite;
}


/* A4 Paper Size */
@media print {
  .container {
    max-width: 100%;
    padding: 0;
  }

  .card {
    width: 210mm;
    height: 297mm;
    margin: 0 auto;
    page-break-before: always;
    page-break-after: always;
    border: none;
    box-shadow: none;
  }

  .btn {
    display: none; /* Hide print button */
  }
  
  /* Prevent elements from breaking awkwardly in print */
  table {
    page-break-inside: avoid;
  }

  tr {
    page-break-inside: avoid;
  }
  
  th, td {
    padding: 8px !important;
  }
}
